*{
  margin: 0px;
  padding: 0px;

}


.localColor{
  background-color: #f9b524;
}
.colorText{
  color: #f9b524;
}
.item-size{
  height: 120px !important;
  width: 120px !important;
}
.cover{
  object-fit:cover;
}

.cards-wrapper {
  display: flex;
  justify-content: center;
}
.card img {
  max-width: 100%;
  max-height: 100%;
}

.card {
  margin: 0 0.5em;
  box-shadow: 2px 6px 8px 0 rgba(22, 22, 26, 0.18);
  border: none;
  border-radius: 0;
}
.carousel-inner {
  padding: 1em;
}

.carousel-control-prev,
.carousel-control-next {
  background-color: #e1e1e1;
  width: 5vh;
  height: 5vh;
  border-radius: 50%;
  top: 50%;
  transform: translateY(-50%);
}


@media (min-width: 768px) {
  .card img {
    height: 11em;
  }
}
.pointer{
  cursor: pointer;
}


.alto{
  height: 300px !important;
  object-fit:cover;
}
.button-radius{
  border-radius: 130px;
  padding: 10px;
  font-size: 12px;

}
.active-button{
  border-color: black !important;
  font-weight: bold;
  border: 1px solid black;

}
.img-alto{
  height: 350px;
  object-fit: cover !important;
}
.z-index{
  z-index: 9999;
}
.z-index-modal{
  z-index: 99999999;
}
.carousel-item .cards-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
}
.circle-card {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  overflow: hidden;
}
.circle-card img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}